import { Connect3Pod } from "../../libraries/Connect3Pod";

const CorePaginator = (paginator, data, callback)=>{
    if(paginator.loading === true){ return; }
    paginator.loading = true;
    data.route = paginator.request.url
    topbar.show();
    Connect3Pod(paginator.request.url, {
        data: data,
        ready: (data)=>{
            topbar.hide();
            data = JSON.parse(data);
            let pushedFirst = false;
            if(typeof data.navigation === "undefined"){ return; }
            data.navigation.map((n)=>{
                if(n !== 1 && pushedFirst !== true){                    
                    data.navigation.push(1);
                    pushedFirst = true;
                }
            })
            data.navigation = data.navigation.filter((n)=>{
                if(n <= data.limit){
                    return n;
                }
            })

            
            data.navigation = data.navigation.sort(function(a, b) {
                return a - b;
            });
            paginator.results = data;
            paginator.loading = false;
            callback(data);
            topbar.hide();
        }
    })
}

export {CorePaginator}