<script>
import ModalAlert from './../components/Modals/ModalAlert.svelte';
import SimpleModal from './../components/Modals/SimpleModal.svelte';
import Paginator from './../components/Paginator/Paginator.svelte';
import {CorePaginator} from './../components/Paginator/CorePaginator.js';
import ControllerPaginator from '../components/Paginator/ControllerPaginator.svelte';
import { Turpial } from '../libraries/turpial';
import { AppForms } from "./../libraries/AppForms";
import { Connect3Pod } from '../libraries/Connect3Pod';
import { toast } from '../libraries/toast';
import { parseDate } from '../libraries/parseDate';
import ComissionPanel from './panels/ComissionPanel.svelte';
import DiscountPanel from './panels/DiscountPanel.svelte';
import EventsPanel from './panels/eventsPanel.svelte';

const turpial = new Turpial({
	public_path: "app.3pod.io",
	loadModulesOnRoute: false,
});
let panel = "main";
let addtagform;
let tags;
let memberships = null;
let plans = null;
let api_url = turpial.host+"private/api/v1/superuser/TagManager/";

function addTag( hide ){
    let data = AppForms.retrieve({
        form: addtagform,
        targets: ".capture"
    })
    Connect3Pod([api_url+"TagManager_add"], {
        data: data,
        ready: (data)=>{
            let response = JSON.parse(data[0]);
            toast(response.message, response.status);
            Paginate();
            hide();
        },
    })
        
    setTimeout(() => {
       // hide()
    }, 200);
}

function fixTagName(event){
    let input = event.target;

    input.value = input.value.trim().split("").filter(( letter )=>{
        if(letter.search(/[A-Za-z0-9]| |-/) >= 0){
            return letter
        }
    }).join("").toLowerCase();

}

let paginator = {
    request: {        
        url: `${turpial.host}/private/api/v1/superuser/TagManager/TagManager_list`,
        collection: "Clientes",
        position: 1,
        field: "ingreso_timestamp",
        limit: 20,
        sortby: "desc",
        searchFor: false,
        fromDate: false,
        untilDate: false,
    },
    loading: false,
	results: {data: [], limit: false, navigation: []},
}
let requesting = false;
const Paginate = (request, callback)=>{
    callback = callback||function(){};
    CorePaginator(paginator, {
        data: paginator.request,
    }, (data)=>{        
        paginator = paginator;
        callback(paginator);
        tags = paginator.results.data;
    })
}

let tagSelected = null;

function searchTag(id){
    return tags.find((tag)=>{
        if(id === tag.id){ return tag; }
    }) ?? null;
}

function navigation(position, id){
    let tag = searchTag(id);
    tagSelected = tag;
    panel = position
}

Connect3Pod(`${turpial.host}api/getPlans`, {
    method: "GET",
    ready: (data)=>{
        data = JSON.parse(data[0]);
        memberships = data.response;
        plans = Object.values(memberships).flat();
    },
})

function removetag(id){
    new ModalAlert({
        target: document.body,
        props: {
            content: `¿Deseas eliminar el tag: ${id}?`,
            onConfirm: ()=>{
                Connect3Pod(`${turpial.host}/private/api/v1/superuser/TagManager/TagManager_delete`, {
                    data: {
                        tag_name: id,
                    },
                    ready: (data)=>{
                        data = JSON.parse(data[0]);
                        toast(data.message, data.status);
                        Paginate();
                    },
                })
            },
        }
    })
   
}

</script>
{#if memberships !== null}
    <section class="columns is-multiline">
        <div class="column is-12">
            <div class="box">
                {#if panel === "main"}
                    <SimpleModal let:hide={hide} closeOnClickOverlay={true}>
                        <svelte:fragment slot="trigger">
                            <button class="button is-primary is-outlined button-default is-rounded is-small">
                                Añadir etiqueta
                            </button>
                        </svelte:fragment>
                        <svelte:fragment slot="content">
                            <form class="columns is-multiline is-centered" action="POST" on:submit|preventDefault="{()=>{addTag( hide )}}" bind:this="{addtagform}">
                                <div class="column is-12">
                                    <h2 class="fw600 has-text-centered has-text-info">
                                        Añade una etiqueta para configurar
                                    </h2>
                                </div>
                                <div class="column is-6">
                                    <label for="">Añade un nombre de etiqueta</label>
                                    <div class="control">
                                        <input type="text" name="tag_name" on:input="{fixTagName}" class="input is-rounded capture" required>
                                    </div>
                                </div>
                                <div class="column is-12">
                                    <div class="control has-text-centered">
                                        <button type="submit" class="button is-primary is-rounded is-small button-default">
                                            Añadir
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </svelte:fragment>
                    </SimpleModal>
                    <div class="columns is-multiline">
                        <div class="column">
                            <Paginator paginator={paginator} Paginate={Paginate}>
                                <div class="columns is-multiline">
                                    <div class="column is-12">
                                        <table class="table table-tag-manager is-hoverable is-fullwidth is-narrow">
                                            <thead>
                                                <th>
                                                    Etiqueta
                                                </th>
                                                <th>
                                                    Creación
                                                </th>
                                                <th class="has-text-right">
                                                    Opciones
                                                </th>
                                            </thead>
                                            <tbody>
                                                {#each paginator.results.data as tag}
                                                    <tr>
                                                        <td>
                                                            <span class="tag tripod is-primary is-rounded">
                                                                {tag.id}
                                                            </span>
                                                        </td>
                                                        <td>
                                                            {parseDate.onlydate(tag.time)}
                                                        </td>
                                                        <td class="has-text-right">
                                                            <span data-tooltip="Configura un plan de comisiones" class="text-icon is-size-7 pointer" on:click="{()=>{navigation("ComissionPanel", tag.id)}}">
                                                                <span class="icon is-small has-text-primary">
                                                                    <i class="fas fa-money-bill-wave-alt"></i>
                                                                </span>
                                                            </span>
                                                            <span data-tooltip="Configura un plan de descuentos" class="text-icon is-size-7 pointer" on:click="{()=>{navigation("DiscountPanel", tag.id)}}">
                                                                <span class="icon is-small has-text-primary">
                                                                    -
                                                                    <i class="fas fa-percentage"></i>
                                                                </span>
                                                            </span>
                                                            <span data-tooltip="Eventos generados" class="text-icon is-size-7 pointer" on:click="{()=>{navigation("EventsPanel", tag.id)}}">
                                                                <span class="icon is-small has-text-primary">
                                                                    <i class="fas fa-calendar-day"></i>
                                                                </span>
                                                            </span>
                                                            <span on:click="{()=>{removetag(tag.id)}}" class="icon is-size-7 is-small pointer has-text-danger">
                                                                <i class="fas fa-trash"></i>
                                                            </span>
                                                        </td>
                                                    </tr>
                                                {/each}
                                            </tbody>
                                        </table>
                                    </div>
                                    <div class="column is-12">
                                        <div class="column is-12">
                                            <ControllerPaginator paginator={paginator} Paginate={Paginate} ></ControllerPaginator>
                                        </div>
                                    </div>
                                </div>
                            </Paginator>
                        </div>
                    </div>  
                {/if}
                {#if panel === "ComissionPanel"}
                    <ComissionPanel tag={tagSelected} plans={plans} api_url={api_url} turpial={turpial} navigation={navigation} />
                {/if}
                {#if panel === "DiscountPanel"}
                    <DiscountPanel tag={tagSelected} plans={plans} api_url={api_url} turpial={turpial} navigation={navigation} />
                {/if}
                {#if panel === "EventsPanel"}
                    <EventsPanel tag={tagSelected} plans={plans} api_url={api_url} turpial={turpial} navigation={navigation} />
                {/if}
            </div>
        </div>
    </section>
{/if}

<style>
.table-tag-manager{
    margin-top: 1rem;
}
.table-tag-manager th{
    color: var(--color-info);
    font-weight: 400;
}
</style>