<script>
import { AppForms } from "../../libraries/AppForms";
import { Connect3Pod } from "../../libraries/Connect3Pod";
import { selectData } from "../../libraries/selectData";
import { toast } from "../../libraries/toast";


export let tag;
// export let turpial;
export let navigation;
export let plans;
export let api_url;
let form = null;
let inputDefault;
let defaultPercent = tag?.affiliatePercent ?? 20;
let mountedData = false;

setTimeout(() => {
    mountedData = true;
    defaultPercent = tag?.affiliatePlan?.affiliatePercent ?? 20;
}, 50);

tag.affiliatePercent = tag.affiliatePercent ?? defaultPercent;
tag.affiliatePlan = tag.affiliatePlan ?? {};
let post_data = {};
tag.affiliatePlan = typeof tag.affiliatePlan === "string" ? JSON.parse(tag.affiliatePlan) : tag.affiliatePlan;
if(tag.affiliatePlan === null || tag.affiliatePlan === false){
    tag.affiliatePlan = {};
}
post_data = {
    affiliatePercent: tag.affiliatePercent,
    plans: tag?.affiliatePlan?.plans || [],
};
if(Array.isArray(post_data.plans)){
    let object = {};
    post_data.plans.map((data)=>{
        object[data.plan] = data;
    });
    post_data.plans = object;
}
tag = tag;



function addPercentage(){
    let data = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    })
    let percent = data.percent ?? defaultPercent;
    let plan = selectData(plans, "__id__", data.plan);
    

    post_data.plans[data.plan] = {
        plan: plan.__id__,
        membership_type: plan.membership_type,
        affiliatePercent: data.percentPlan
    };    
    post_data = post_data;

    
    tag.affiliatePlan.plans = post_data.plans;
    tag = tag;
}

function removePercentage(id){
    delete tag.affiliatePlan.plans[id];
    tag = tag;
}


function submit(){
    post_data.affiliatePercent = defaultPercent;
    let data = {
        affiliatePlan: post_data,
        tag_name: tag.id,
    }
    data.affiliatePlan.plans = Object.values(data.affiliatePlan.plans)
    Connect3Pod(api_url+"TagManager_update", {
        data: data,
        ready: (data)=>{
            data = JSON.parse(data[0]);
            toast(data.message, data.status)
        }
    })
}


let membership_names = {}

plans = plans.filter(( plan )=>{
    let type = plan.membership_type
    switch (type) {
        case "funnels":
            plan.membership_name = "Funnels"
        break;
        case "designs":
            plan.membership_name = "Diseño Gráfico"
        break;
        case "campaigns":
            plan.membership_name = "Campañas"
        break;
        default:
        plan.membership_name = "Sin categoría"
        break;
    }


    return plan
})


</script>
{#if mountedData === true}
    <section>
        <header class="columns is-multiline">
            <div class="column is-12">            
                <div class="buttons is-right">
                    <div class="delete pointer has-background-danger" on:click="{navigation("main")}"></div>
                </div>
                <h2 class="has-text-centered is-size-5 fw600 has-text-info">
                    Plan de comisiones
                    <br>
                    <span class="tag is-primary is-rounded is-medium text-icon tripod">
                        <span class="icon">
                            <i class="fas fa-tag"></i>
                        </span>
                        <span>                        
                            {tag.id}
                        </span>
                    </span>
                </h2>
            </div>
            <div class="column is-12">
                <h2>
                    
                </h2>
            </div>
        </header>
        <main>
            <form class="columns is-multiline is-centered" action="POST" bind:this="{form}" on:submit|preventDefault="{(e)=>{submit(e.target)}}">                        
                <div class="field column is-6">
                    <label for="" class="fw600 label-text">Porcentaje por default</label>
                    <div class="control has-text-centered has-icons-left">                                
                        <input type="number" step="any" bind:this="{inputDefault}" bind:value="{defaultPercent}" name="affiliatePercent" placeholder="%" class="input formElements is-rounded is-hovered" min="1" max="99" required>
                        <span class="icon is-small is-left">
                            <i class="fas fa-percent"></i>
                        </span>                                
                    </div>
                </div>
                <div class="field column is-8">
                    <h2 class="has-text-dark is-size-6 fw600 has-text-centered">
                        Plan de afiliados
                    </h2>
                </div>
                <div class="column is-8">
                    <div class="field has-addons is-justify-content-center">
                        <div class="control" style="width: 40%;">
                            <div class="select is-rounded">
                                <select name="plan" class="formElements">
                                    {#each plans as plan}
                                        <option data-membership={plan.membership_type} value="{plan.__id__}">{plan.membership_name} - {plan.name_plan}</option>
                                    {/each}
                                </select>
                            </div>
                        </div>
                        <div class="control has-icons-left">
                            <input type="number" name="percentPlan" value="20" inputmode="numeric" class="input is-rounded formElements" min="1" max="99" step="any" placeholder="%">
                            <span class="icon is-small is-left">
                                <i class="fas fa-percent"></i>
                            </span>
                        </div>
                        <div class="control">
                            <button on:click="{()=>{addPercentage(form)}}" type="button" class="button is-primary is-outlined is-rounded">
                                Agregar
                            </button>
                        </div>
                    </div>
                </div>                     
            </form>     
            {#if tag.affiliatePlan.length > 0}
                <h3 class="has-text-centered fw600 has-text-info">
                    Planes con porcentaje personalizado establecidos:
                </h3>
            {/if}
            <div class="columns is-multiline is-centered">
                {#each Object.values(tag?.affiliatePlan?.plans || []) as plan}
                    <div class="column is-8">
                        <div class="box box-plans-added">
                            <div class="delete has-background-danger delete-item" on:click="{()=>{removePercentage(plan.plan)}}"></div>
                            <div class="columns is-multiline">
                                <div class="column">
                                    Plan: <strong>
                                        {selectData(plans, "__id__", plan.plan).name_plan}
                                    </strong>
                                </div>
                                <div class="column">
                                    <p class="has-text-primary fw600">
                                        + {plan.affiliatePercent}%
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                {/each}
            </div>
            <p class="has-text-centered is-size-7">
                Los planes a los que no se les establezca un porcentaje, mantendrán el porcentaje por default.
            </p>
            <br>
            <div class="buttons is-centered">
                <button type="submit" on:click="{()=>{submit(form)}}" class="button is-success fw600 is-rounded has-text-white">
                    Guardar
                </button>
            </div>
        </main>
    </section>
{/if}

<style>
.box-plans-added{
    position: relative;
}
.delete-item{
    position: absolute;
    top: 0.25rem;
    right: 0.25rem;
}
</style>