import { turpial } from "./turpial";
const toast = function(html, type, close,position) {
    function execute(){
        html = html||"";type = type||"success";close = close||3500;position = position||"top-right";
        spop({ template: html, style: type, position: position, autoclose: close });
    }
    if(typeof spop !== "undefined"){
        execute()
    }else{        
        turpial.fetch({
            url: [HOST+"/assets/js/spop/spop.min.js"],
            ready: ()=>{}
        })
        turpial.includeCSS({
            url: [HOST+"/assets/js/spop/spop.min.css"],
            ready: ()=>{
                execute();
            }
        })
    }
}
export {toast};