<script>
export let Paginate;
export let paginator;
export let request_url = paginator.request_url;
paginator.request.real_path = request_url;
// first search
Paginate((PAGINATOR)=>{
    paginator = PAGINATOR;
    paginator = paginator;
})

</script>
{#if (paginator?.results?.data||[]).length > 0}
    <slot paginator={paginator}></slot>
{/if}
