<script>
export let paginator;
export let Paginate;


let navigation = paginator.results.navigation;
let currentPosition = paginator.results.position||"1";
let max = paginator.results.limit||"1"; // max number navigation
let min = "1"; // min number navigation

let previus = paginator.results.position-1;
let next = paginator.results.position+1;
let hasPrevius = true;
let hasNext = false;
function setPaginatePos(position){
    if(position < 1){ return; }
    if(position > paginator.results.limit){ return; }
    paginator.request.position = +(position);    
    Paginate(paginator, (PAGINATOR)=>{
        paginator = PAGINATOR;
        paginator = paginator;
        previus = paginator.results.position-1;
        if(previus > 0){
            hasPrevius = paginator.results.navigation.find(( pos )=>{
                if( +(pos) === previus ){
                    return true;
                }
            })||false;
        }
    })
}
</script>
<style>
button{
	box-shadow: 5px 5px 32px rgba(142, 142, 142, 0.29);
    border: 1px solid rgba(142, 142, 142, 0.50) !important;
}
.button:hover{
    background: inherit;
}
</style>
<slot paginator={paginator}></slot>
<div class="buttons is-grouped is-centered">    
    <button type="button" on:click="{ (e)=>{ setPaginatePos( (+(paginator.results.position))-1 ) }}" class="button is-outlined">
        <span class="has-text-primary">
            ◂
        </span>
    </button>
    {#if +(paginator.results.position) > 1 && +(paginator.results.position) !== +(paginator.results.limit)}
        <button class="button is-outlined has-text-primary" on:click="{ (e)=>{ setPaginatePos(1) }}" type="button" data-position="1">
            1...
        </button>
    {/if}
    {#if hasPrevius === false && previus !== 0}
        <button class="button is-outlined has-text-primary" on:click="{ (e)=>{ setPaginatePos(previus) }}" type="button" data-position={previus}>
            {previus}
        </button>
    {/if}
    {#if +(paginator.results.position) !== +(paginator.results.limit) && paginator.results.position !== 0}
        <button class="button is-primary" type="button" data-type="after-previus" data-position={paginator.results.position}>
            {paginator.results.position}
        </button>
    {/if}
    {#each paginator.results.navigation as button, key}
        {#if +(button) !== 1 && +(button) !== +(paginator.results.limit)}
            <button class="button is-outlined has-text-primary" on:click="{ (e)=>{ setPaginatePos(button) }}" type="button" data-position={button}>
                {button}
            </button>
        {/if}
    {/each}
    {#if +(paginator.results.position) < +(paginator.results.limit)}
        <button class="button is-outlined has-text-primary" on:click="{ (e)=>{ setPaginatePos(e.target.dataset.position) }}" type="button" data-position={paginator.results.limit}>
            ...
            {paginator.results.limit}
        </button>
    {/if}
    {#if +(paginator.results.position) === +(paginator.results.limit)}
        <button class="button is-primary" type="button" data-position={paginator.results.position}>
            ...
            {paginator.results.position}
        </button>
    {/if}

    <button on:click="{ (e)=>{ setPaginatePos( (+(paginator.results.position))+1 ) }}" type="button" class="button is-outlined has-background-white">
        <span class="has-text-primary">
            ▸
        </span>
    </button>
</div>